import { useState, useEffect } from 'react'
import { getAllPosts } from './service'


const NewsList = () => {
    const [articles,setArticles] = useState([])
    const [visible,setVisible] = useState(10)
    const [search,setSearch] = useState('')

    const showMoreBlogs = () => {
        setVisible(prev => prev + 5)
    }
    
    useEffect(() => {
        getAllPosts().then(res => {
           console.log(res.data[0])
            setArticles(res.data)
        })
    },[])
    


    return (
        <div className='grid'>    
        <div className='header'>
            <header>
            <input className='input-search' type='search' placeholder='Search title...' onChange={(e) => {setSearch(e.target.value)}} />

        </header>
            <h2 className='main-title'>New Blogs</h2>
            {
               articles.filter((value) => {
                   if(search === '') {
                      return value
                   }else if (value.title.toLowerCase().includes(search.toLowerCase())) {
                       return value
                   }  else {
                    return    
                   }
                               
               }).slice(0,visible).map(article => {
                   return(
                       <div  key={article.id}  className='div-flex'>
                       <div className='mainDiv'>
                           <h5>{article.title}</h5>
                           <img src={article.image} alt={'New'} />
                       </div>
                       <div className='mainDiv-1'>
                            <p>{article.description}</p>
                            <p style={{color:'#fecc2c'}}>{article.date}</p>
                       </div>
                       <sidebar className='mainDiv-2'>
                           <span>Number{article.id}</span>
                            <a href={article.description}>{article.description}</a>
                            <p style={{color:'#fecc2c'}}>{article.date}</p>
                       </sidebar>
                       </div>
                   )
               })  
            }
           
        </div>
        
        <button className='load-more' onClick={showMoreBlogs}>Load more...</button>
        </div>
    )
}

export default NewsList